import { environment } from './../../../environments/environment';
import { Injectable } from '@angular/core';
import {
	HttpClient,
	HttpHeaders,
	HttpErrorResponse,
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

@Injectable({
	providedIn: 'root',
})
export class ParametrosService {
	apiURL: string;

	httpOptions = {
		headers: new HttpHeaders({
			'Content-Type': 'application/json',
		}),
	};

	constructor(private http: HttpClient) {
		this.apiURL = environment.urlApiParametros;
	}

	getParametros(): Observable<any> {
		return this.http.get(this.apiURL).pipe(
			retry(1),
			catchError(this.handleError),
		);
	}

	getParametrosIndex(index: string): any[] {
		let parametrosAll: any[] = [];
		this.http
			.get(this.apiURL)
			.pipe(
				retry(1),
				catchError(this.handleError),
			)
			.subscribe((datos: any) => {
				parametrosAll = datos.parameters;
				return parametrosAll.filter((x) => (x.keyParameter = index));
			});

		return [];
	}

	handleError(error: HttpErrorResponse) {
		return throwError(error);
	}
}
