import { Injectable } from '@angular/core';
import { JwksValidationHandler, OAuthService } from 'angular-oauth2-oidc';
import { authConfig } from '../../models/auth/auth.config';
import { Observable, of } from 'rxjs';
import { filter, mergeMap, tap } from 'rxjs/operators';
import { isNullOrUndefined } from 'util';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Owner } from '../../models/owner/owner';
import { LoaderService } from '../loader/loader.service';

@Injectable({
	providedIn: 'root',
})
export class AuthenticationService {
	public authHeader = null;
	public currentManager: Owner;
	private claims = null;
	private autoCounter = 0;
	constructor(
		private oauthService: OAuthService,
		private router: Router,
		private loaderService: LoaderService,
	) {
		this.configureWithNewConfigApi();
	}

	public login() {
		sessionStorage.removeItem('skipalert');
		this.oauthService.initImplicitFlow();
	}

	public logOut() {
		this.loaderService.toggleLoader(true);
		this.handleLogout();
	}

	public verifySession(): Observable<boolean> {
		return of(this.validateAuthToken()).pipe(
			mergeMap((isValidAuthToken) => this.getClaims(isValidAuthToken)),
			mergeMap((claimsExists) => this.setManagerData(claimsExists)),
		);
	}

	private validateAuthToken(): boolean {
		this.claims = this.oauthService.getIdentityClaims();
		this.autoCounter++;
		if (
			isNullOrUndefined(this.claims) &&
			this.autoCounter > 2 &&
			!/access_token/.test(window.location.href)
		) {
			this.router.navigate(['/']);
		}
		this.authHeader = {
			headers: new HttpHeaders({
				Authorization: `Bearer ${this.oauthService.getIdToken()}`,
			}),
		};
		return (
			!isNullOrUndefined(this.claims) &&
			!isNullOrUndefined(this.oauthService.getAccessToken())
		);
	}

	private getClaims(isValidAuthToken: boolean): Observable<boolean> {
		if (!isValidAuthToken) {
			const uri = window.location.href;
			const result = uri.match(/customErrorFOCC-(.*?)\./);

			if (!isNullOrUndefined(result) && result.length > 0) {
				return of(null);
			}

			return this.oauthService.events.pipe(
				filter((oAuthEvent) => this.validateAuthToken()),
				mergeMap((_) => of(!isNullOrUndefined(this.claims))),
			);
		} else {
			return of(isValidAuthToken);
		}
	}

	private setManagerData(claimsExists: boolean): Observable<boolean> {
		if (claimsExists) {
			this.currentManager = isNullOrUndefined(this.currentManager)
				? {
						firstName: this.claims.given_name,
						email: this.claims.email,
						// tslint:disable-next-line: indent
				  }
				: this.currentManager;
			return of(true);
		} else {
			of(false);
		}
	}

	private handleLogout() {
		this.oauthService.logoutUrl = this.oauthService.loginUrl.replace(
			'oauth2/authorize',
			`logout?client_id=${this.oauthService.clientId}&logout_uri=${this.oauthService.postLogoutRedirectUri}`,
		);
		this.oauthService.logOut();
	}

	private configureWithNewConfigApi() {
		this.oauthService.configure(authConfig);
		this.oauthService.tokenValidationHandler = new JwksValidationHandler();
		this.oauthService.loadDiscoveryDocumentAndTryLogin();
	}
}
