import {
	Directive,
	Input,
	EventEmitter,
	Output,
	Renderer2,
	ElementRef,
	HostListener,
} from '@angular/core';

@Directive({
	// tslint:disable-next-line: directive-selector
	selector: 'input[tipo-dato]',
})
export class ValidarInputDirective {
	@Input('tipo-dato') tipoDato: string;
	@Input('regExp') expresion: any;
	@Output() ngModelChange: EventEmitter<any> = new EventEmitter();

	private patronSoloNumero = /[^0-9]/g;
	private patronAlfanumerico = /[^0-9A-Z a-zÑñáéíóúÁÉÍÓÚ]/g;
	private patronAlfanumericoGuion = /[^0-9A-Z a-zÑñáéíóúÁÉÍÓÚ-]/g;
	private patronAlfanumericoGuionBackSlash = /[^/ 0-9A-Z a-zÑñáéíóúÁÉÍÓÚ-]/g;
	private patronFormato24 = /[^0-9:]/g;
	private patronNumericoPunto = /[^0-9.]/g;
	private patronTexto = /[^A-Z a-zÑñáéíóúÁÉÍÓÚ]/g;
	private patronFecha = /[^0-9/]/g;
	private patronSoloLetras = /[^A-Z a-zÑñáéíóúÁÉÍÓÚ]/g;
	private patronEmail = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{4,63}$/i;

	constructor(private elRef: ElementRef, private renderer: Renderer2) {
		this.expresion = null;
	}

	@HostListener('keyup', ['$event.target']) onKeyUp(elemento) {
		elemento.value = this.getValorValidado(elemento.value);
		this.ngModelChange.emit(elemento.value);
	}

	/**
	 * Evento ejecutado al quitar foco del elemento
	 * @param elemento
	 */
	@HostListener('blur', ['$event.target']) onBlur(elemento) {
		elemento.value = this.getValorValidado(elemento.value);
		this.ngModelChange.emit(elemento.value);
	}

	getValorValidado(valorIn: any) {
		let valorFinal: any = null;

		switch (this.tipoDato) {
			case 'numerico':
				valorFinal = (valorIn + '').replace(this.patronSoloNumero, '');
				break;
			case 'alfanumerico':
				valorFinal = (valorIn + '').replace(this.patronAlfanumerico, '');
				break;
			case 'fecha':
				valorFinal = (valorIn + '').replace(this.patronFecha, '');
				break;
			case 'alfanumericoGuion':
				valorFinal = (valorIn + '').replace(this.patronAlfanumericoGuion, '');
				break;
			case 'alfanumericoGuionBackSlash':
				valorFinal = (valorIn + '').replace(
					this.patronAlfanumericoGuionBackSlash,
					'',
				);
				break;
			case 'formato24':
				valorFinal = (valorIn + '').replace(this.patronFormato24, '');
				break;
			case 'numericoPunto':
				valorFinal = (valorIn + '').replace(this.patronNumericoPunto, '');
				break;
			case 'texto':
				valorFinal = (valorIn + '').replace(this.patronTexto, '');
				break;
			case 'parametro':
				valorFinal = (valorIn + '').replace(this.expresion, '');
				break;
			case 'numericoPuntoDuo':
				valorFinal = this.validacionDecimalesEnteros(valorIn);
				break;
			case 'soloAlfabeto':
				valorFinal = (valorIn + '').replace(this.patronSoloLetras, '');
				break;
			case 'email':
				valorFinal = (valorIn + '').replace(this.patronEmail, '');
				break;

			default:
				valorFinal = valorIn;
				break;
		}

		return valorFinal;
	}

	validacionDecimalesEnteros(Dato: any) {
		let valorRespuesta: any = null;
		const regPrimera = /^[0-9]{1,2}$/g;
		const regSegunda = /^[0-9]{1,2}[.]{1}$/g;
		const regTercera = /^[0-9]{1,2}[.]{1}[0-9]{1,2}$/g;

		if (regPrimera.test(Dato)) {
			valorRespuesta = Dato;
		} else if (regSegunda.test(Dato)) {
			valorRespuesta = Dato;
		} else if (regTercera.test(Dato)) {
			valorRespuesta = Dato;
		} else {
			valorRespuesta = Dato.substring(0, Dato.length - 1);
		}
		return valorRespuesta;
	}
}
