import { Component, OnInit, Input } from '@angular/core';
import { LoaderService } from '../../services/loader/loader.service';

@Component({
	selector: 'app-focc-loader',
	templateUrl: './loader.component.html',
	styleUrls: ['./loader.component.scss'],
})
export class LoaderComponent implements OnInit {
	public lottieConfig: object;
	public showLoader = false;
	@Input() options: string;

	constructor(private loaderService: LoaderService) {
		this.lottieConfig = {
			path: 'assets/pid/lottie/spinner.json',
			renderer: 'canvas',
			autoplay: true,
			loop: true,
		};
		this.loaderService.componentMethodCalled$.subscribe((action) => {
			this.actionLoader(action);
		});
	}

	ngOnInit() {}

	private actionLoader(action = false) {
		this.showLoader = action !== false ? true : action;
	}
}
