import { TipoMensajes } from './../../shared/enums/mensajes.enum';
import { EstadosRespuesta } from './../../shared/enums/estados.enum';
import { ParametrosService } from './../../shared/services/parametros.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare let $: any;

@Component({
	selector: 'app-validarpep',
	templateUrl: './validarpep.component.html',
	styleUrls: ['./validarpep.component.css'],
})
export class ValidarpepComponent implements OnInit {
	public esReconPublico?: boolean;
	public esResponTributaria?: boolean;
	public esMonExtranjera?: boolean;
	public habilitarBoton: boolean;

	public listaPreguntas: any[];
	public listaValidaPreguntas: any[];

	public tituloAlerta: string;
	public mensajeAlerta: string;

	constructor(
		private router: Router,
		private parametrosSvc: ParametrosService,
	) {
		this.esReconPublico = null;
		this.esResponTributaria = null;
		this.esMonExtranjera = null;
		this.tituloAlerta = null;
		this.mensajeAlerta = null;
		this.listaPreguntas = [];
		this.listaValidaPreguntas = [];
		this.habilitarBoton = false;
	}

	ngOnInit() {
		this.parametrosSvc.getParametros().subscribe((data) => {
			if (data.status === EstadosRespuesta.OK) {
				const parametrosAll = data.parameters;
				this.listaPreguntas = parametrosAll.filter(
					(x) => x.keyParameter === 'PEP_PREGUNTA',
				)[0].items;
			}
		});
	}

	validarItems(pregunta: string, valor: boolean) {
		const existe = this.listaValidaPreguntas.filter(
			(x) => x.pregunta === pregunta,
		)[0];
		if (existe != null) {
			existe.valor = valor;
		} else {
			this.listaValidaPreguntas.push({ pregunta, valor });
		}

		if (this.listaValidaPreguntas.length === this.listaPreguntas.length) {
			this.habilitarBoton = true;
		}
	}

	validarPersonaPep() {
		if (this.listaValidaPreguntas.filter((x) => x.valor === true).length > 0) {
			this.router.navigateByUrl(`/mensaje/${TipoMensajes.ErrorPep}`);
		} else {
			this.router.navigateByUrl(`/identidad`);
		}
	}
}
