import { ValidaridentidadComponent } from './pages/validaridentidad/validaridentidad.component';
import { MensajeComponent } from './pages/mensaje/mensaje.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginPageComponent } from './pages/login-page/login-page.component';
import { AuthGuardService } from './shared/services/auth-guard/auth-guard.service';
import { ValidarpepComponent } from './pages/validarpep/validarpep.component';

const routes: Routes = [
	{
		path: '',
		component: LoginPageComponent,
		data: { pos: 0 },
	},
	{
		path: 'inicio',
		component: ValidarpepComponent,
		canActivate: [AuthGuardService],
		data: { pos: 1 },
	},
	{
		path: 'mensaje/:tipomensaje',
		component: MensajeComponent,
		canActivate: [AuthGuardService],
		data: { pos: 2 },
	},
	{
		path: 'identidad',
		component: ValidaridentidadComponent,
		canActivate: [AuthGuardService],
		data: { pos: 3 },
	},
	{
		path: '**',
		component: LoginPageComponent,
	},
];

@NgModule({
	imports: [
		RouterModule.forRoot(routes, {
			scrollPositionRestoration: 'enabled',
		}),
	],
	exports: [RouterModule],
})
export class AppRoutingModule {
	routes;
}
